import { ToastService } from 'src/app/services/toast.service';
import { ApiLMSService } from 'src/app/services/api-lms.service';

import { ApiGeneralService } from 'src/app/services/api-general.service';
import { LoaderService } from 'src/app/services/loader.service';
import { Component, OnInit } from '@angular/core';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { environment } from 'src/environments/environment.prod';
import { AlertController } from '@ionic/angular';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-scholarship',
  templateUrl: './scholarship.page.html',
  styleUrls: ['./scholarship.page.scss'],
})
export class ScholarshipPage implements OnInit {

  webAssetsPath = "";
  webAssetsPathReviews = "";
  arrContent = [];
  arrReviews = [];
  mobileCatID = "";
  currentSelected = 0;
  SubSelectedTab = "a1";
  costContent = "";
  slideOpts = {
    slidesPerView: 3.5,
    freeMode: true
  }
  slideReviewOpts = {slidesPerView: 1.2}
  slideCountryOpts = {slidesPerView: 3.5}
  slideScholarshipOpts = {slidesPerView: 1.1}

  arrScholarships = [];
  

  //#region  Video Variables
  webVideoPath="";
  arrVideos = [];
  isVideoOpen : boolean ;
  iframe : any;  
  slideVideoOpts = {
    slidesPerView: 1.5    
  }

  currentYear : any;
  //#endregion

  constructor(
    private headerTitle: HeaderTitleService,
    private apiGeneral: ApiGeneralService,
    private loader: LoaderService,
    public alertController: AlertController,
    private apiLMSService : ApiLMSService,
    private apiGeneralService : ApiGeneralService,
    private toastr : ToastService,
    private sanitizer : DomSanitizer
  ) {
    this.headerTitle.isBack = false;
    this.headerTitle.title = "Scholarship";
  }

  ngOnInit() {
    this.getScholarships();
    this.getContent();
    this.getReview();
    this.getVideos();
    //this.getIntersetedProgram();

    this.currentYear = new Date().getFullYear();
  }

  getScholarships(){
    this.arrScholarships = this.apiLMSService.arrScholarships;
  }

  //#region Content Start
  
  getContent() {
    
    let postData = { "PageName": "Scholarship", "IsActive": true }
    this.apiGeneral.getContent(JSON.stringify(postData)).then(resp => {
      this.arrContent = resp;
      this.costContent = this.arrContent[0]["details"];
      this.webAssetsPath = environment.webAssetsPath + "mobility-app/category-contents";      
    })
      .catch(err => {
        console.log("getContent: " + err);        
      })
  }
  ionSelectProgram(mobileCatID, index) {
    this.mobileCatID = mobileCatID;
    this.currentSelected = index;
    this.arrContent.forEach(i => {
      if (i.mobileCatID == mobileCatID) {
        this.costContent = i.details
      }
    });
  }
  subTab(subTabId) {
    this.SubSelectedTab = subTabId;
    console.log(subTabId);
  }

  //#endregion


  getReview() {
    let postData = { "Category": "Scholarship", "IsActive": true }
    this.apiGeneral.getReviews(JSON.stringify(postData)).then(resp => {
      this.arrReviews = resp;      
      this.webAssetsPathReviews = environment.webAssetsPath + "mobility-app/student-review";      
    })
      .catch(err => {
        console.log("getReview: " + err);       
      })
  }

  //#region  Video Section start
  async getVideos() {
    let postData = { 'videoHeaderId': 25, 'isActive':true}
    await this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.webVideoPath = environment.webAssetsPath+ "mobility-app/videothumbnails";
      this.arrVideos = resp;
    })
      .catch(err => {
        console.log("getVideos: " + err);
      })
  }
  
  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //#endregion

}
